import uuid from 'react-uuid';
import './schedule-inputs.css';

const scheduleInputs = ({formData, setFormData, setSchedule, handleImageDownload})=>{

    function handleChange(e){
        const {name, value} = e.target
        setFormData(prevFormData =>{
            return{
                ...prevFormData,
                [name]: value,
                id: uuid()
            }
        })    
    }

    function handleSubmit(e){
        e.preventDefault();
        setSchedule(prevSchedule =>{
            return[...prevSchedule, formData] 
        });
    }

    return(
        <div className="schedule-maker">
            <form action="#">
                <input 
                    type="text" 
                    placeholder=" ..."
                    name='lesson'
                    value={formData.lesson}
                    onChange={handleChange}/>
                <div className='select-inputs'>
                    <div className="select-group select1">
                        <label htmlFor="week">
                             :
                        </label>
                        <select 
                            id="week"
                            name='day'
                            value={formData.day}
                            onChange={handleChange}>
                            <option value=''>---</option>
                            <option value="">
                                
                            </option>
                            <option value=" ">
                                 
                            </option>
                            <option value="">
                                
                            </option>
                            <option value=" ">
                                 
                            </option>
                            <option value="">
                                
                            </option>
                            <option value=" ">
                                
                            </option>
                            <option value="">
                                
                            </option>
                        </select>
                    </div>
                    <div className="select-group select2">
                        <label htmlFor="time-start">
                             :
                        </label>
                        <select 
                            id="time-start"
                            name='startTime'
                            value={formData.startTime}
                            onChange={handleChange}>
                            <option value=''>---</option>
                            <option value="8">08</option>
                            <option value="9">09</option>
                            <option value="10">10</option>
                            <option value="11">11</option>
                            <option value="12">12</option>
                            <option value="13">13</option>
                            <option value="14">14</option>
                            <option value="15">15</option>
                            <option value="16">16</option>
                            <option value="17">17</option>
                            <option value="18">18</option>
                            <option value="19">19</option>
                            <option value="20">20</option>
                        </select>
                    </div>
                    <div className="select-group select3">
                        <label htmlFor="time-end">
                             :
                        </label>
                        <select 
                            id="time-end"
                            name='endTime'
                            value={formData.endTime}
                            onChange={handleChange}>
                            <option value=''>---</option>
                            <option value="8">08</option>
                            <option value="9">09</option>
                            <option value="10">10</option>
                            <option value="11">11</option>
                            <option value="12">12</option>
                            <option value="13">13</option>
                            <option value="14">14</option>
                            <option value="15">15</option>
                            <option value="16">16</option>
                            <option value="17">17</option>
                            <option value="18">18</option>
                            <option value="19">19</option>
                            <option value="20">20</option>
                        </select>
                    </div>
                    <div className="select-group select4">
                        <label htmlFor="color">
                            :
                        </label>
                        <select
                            id="color"
                            name='color'
                            value={formData.color}
                            onChange={handleChange}>
                            <option value=''>---</option>
                            <option value="">
                                
                            </option>
                            <option value="">
                                
                            </option>
                            <option value="">
                                
                            </option>
                            <option value="">
                                
                            </option>
                            <option value="">
                                
                            </option>
                            <option value="">
                                
                            </option>
                            <option value="">
                                
                            </option>
                            <option value="">
                                
                            </option>
                            <option value=" ">
                                 
                            </option>
                            <option value=" ">
                                 
                            </option>
                            <option value="">
                                
                            </option>
                        </select>
                    </div>
                </div>
                <button onClick={handleSubmit} type="submit" className='add-new-btn'>
                     
                </button>
                <button onClick={handleImageDownload} className="make-schedule-btn" type='button'>
                     
                </button>
            </form>
        </div>
    );
}
export default scheduleInputs;